.btn {
  font-weight: 700;
  color: rgb(84, 84, 84);
  margin-left: 20px;
  margin-top: 20px;
  padding: 5px 10px;
  background-color: #fff;
  border-radius: 5px;
  border-color: transparent;
  box-shadow: rgba(6, 24, 44, 0.4) 0px 0px 0px 2px,
    rgba(6, 24, 44, 0.65) 0px 4px 6px -1px,
    rgba(255, 255, 255, 0.08) 0px 1px 0px inset;
  outline: none;
  transition: 400ms;
}

.btn:hover {
  color: darkred;
}
